import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginGuard } from 'src/app/Guard/login.guard';
import { MyAcCommanComponent } from './my-ac-comman/my-ac-comman.component';
import { MyNotificationComponent } from './my-notification/my-notification.component';
import { MyProfileComponent } from './my-profile/my-profile.component';
import { AcLayoutComponent } from './my_ac_layout/ac-layout/ac-layout.component';
import { OrderInvoicesComponent } from './order-invoices/order-invoices.component';
import { TransactionHistoryComponent } from './transaction-history/transaction-history.component';
import { WishListComponent } from './wishlist/wishlist.component';

const routes: Routes = [
    {
        path: '', component: AcLayoutComponent,
        children: [
            {
                path: 'my-accounts-details', component: MyAcCommanComponent,
            },
            {
                path: 'my-profile',
                component: MyProfileComponent,
                canActivate: [LoginGuard]
            },
            {
                path: 'my-order',
                component: OrderInvoicesComponent,
                canActivate: [LoginGuard]
            },
            {
                path: 'transaction-history',
                component: TransactionHistoryComponent,
                canActivate: [LoginGuard]
            },
            {
                path: 'wish-list',
                component: WishListComponent,
                canActivate: [LoginGuard]
            },
            {
                path: 'my-notification',
                component: MyNotificationComponent,
                canActivate: [LoginGuard]
            },
            {
                path: '',
                redirectTo: '/my-accounts-details',
                pathMatch: 'full'
            }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class MyAccountsRoutingModule { }
