import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { FilterService } from 'src/app/service/filter.service';
import { GlobalService } from 'src/app/service/global.service';

@Component({
    selector: 'app-transaction-history',
    templateUrl: './transaction-history.component.html',
    styleUrls: ['./transaction-history.component.scss']
})

export class TransactionHistoryComponent implements OnInit {
    isDataLoading: boolean = false;
    historyList: any = [];
    maxdate: any = new Date().toISOString().split('T')[0];

    filterObj: any = {
        invoice_date_from: '',
        invoice_date_to: '',
        invoice_number: '',
        sortBy: '',
    }

    constructor(public gs: GlobalService,
        public _route: ActivatedRoute,
        private toastr: ToastrService,
        public filter: FilterService) {

        this._route.queryParamMap.subscribe((params) => {
            let paramsID = params.get('bcid');
            if(paramsID != null){
                this.getAll(paramsID);
            } else {
                this.getAll(this.gs.loginData['_id']);
            }
        });
        
        this.gs.updatePageName('Transaction History');
    }

    changeStartDate() {
        this.filterObj.invoice_date_to = '';
    }

    ngOnInit(): void {

    }

    filterData() {
        if (this.filterObj.invoice_date_from.trim() || this.filterObj.invoice_date_to.trim() || this.filterObj.invoice_number.trim() ||
            this.filterObj.sortBy.trim()) {
            let data = {
                id: this.gs.loginData['_id'],
                invoice_date_from: this.filterObj.invoice_date_from,
                invoice_date_to: this.filterObj.invoice_date_to,
                invoice_number: this.filterObj.invoice_number,
                sortBy: this.filterObj.sortBy
            }
            this.isDataLoading = true;
            this.filter.transactionFilterData(data).subscribe((resp: any) => {
                if (resp['message'] == 'success') {
                    this.historyList = resp.data;
                }
                this.isDataLoading = false;
            }, error => {
                this.isDataLoading = false;
            });
        }
    }

    getAll(id: any) {
        this.isDataLoading = true;
        this.gs.getTransaction(id).subscribe((resp: any) => {
            this.isDataLoading = false;

            if (resp['status']) {
                this.historyList = resp.data;
            } else {
                this.toastr.error(resp['massage'], 'Oops!', {
					timeOut: 5000,
					positionClass: 'toast-top-right',
					progressBar: true,
					progressAnimation: 'increasing'
				});
            }
        }, (error: any) => {
            this.isDataLoading = false;
        });
    }

}
