import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { CommonService } from 'src/app/service/common.service';
import { FilterService } from 'src/app/service/filter.service';
import { GlobalService } from 'src/app/service/global.service';
import { NotificationService } from 'src/app/service/notification.service';
import { SettingService } from 'src/app/service/setting.service';

@Component({
    selector: 'app-my-notification',
    templateUrl: './my-notification.component.html',
    styleUrls: ['./my-notification.component.scss']
})
export class MyNotificationComponent implements OnInit {

    notificationData: any = [];
    isSpinner: boolean = false;
    actionPermissions: any = {};
    filterObj: any = {
        date_from: '',
        date_to: '',
        sort_by: ''
    };

    constructor(public gs: GlobalService,
        private filter: FilterService,
        private toastr: ToastrService,
        private notification: NotificationService,
        private common: CommonService,
        public setService: SettingService) {
        this.gs.updatePageName('System Notifications');
    }

    ngOnInit(): void {
        if (this.gs.loginData && this.gs.loginData['_id']) {
            this.setService.getPermission({ id: this.gs.loginData['_id'] }).subscribe((resp: any) => {
                this.actionPermissions = resp['data'];
            }, (error: any) => {
                console.log(error);
            });
            this.getNotification(this.gs.loginData['_id']);
            this.readNotification(this.gs.loginData['_id']);
        }
    }

    // filter data
    filterData() {
        if (this.filterObj.date_from.trim() || this.filterObj.date_to.trim() || this.filterObj.sort_by.trim()) {
            let data = {
                id: this.gs.loginData['_id'],
                date_from: this.filterObj.date_from,
                date_to: this.filterObj.date_to,
                sort_by: this.filterObj.sort_by,
            }

            this.isSpinner = true;
            this.filter.notificationFilterData(data).subscribe((resp: any) => {
                this.isSpinner = false;
            }, error => {
                this.isSpinner = false;
            });
        }
    }

    // Get Notification
    getNotification(id: any) {
        this.isSpinner = true;
        this.notification.get_system_notification({ individual : id}).subscribe((resp: any) => {
            if (resp['message'] == 'success') {
                this.notificationData = resp.data;
            } else {
                this.toastr.error(resp['message'], 'Oops!', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
            this.isSpinner = false;
        }, (error: any) => {
            this.isSpinner = false;
        });
    }

    // Get Notification
    readNotification(id: any) {
        this.notification.read_system_notification({ individual : id}).subscribe((resp: any) => {
        }, (error: any) => {});
    }

}
