import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GlobalService } from 'src/app/service/global.service';


@Component({
    selector: 'app-my-ac-comman',
    templateUrl: './my-ac-comman.component.html',
    styleUrls: ['./my-ac-comman.component.scss']
})
export class MyAcCommanComponent implements OnInit {

    constructor(
        public gs: GlobalService,
        public router: Router
    ) {
        this.gs.updatePageName('My Account');

        if (!localStorage.getItem('buyerData')) {
            this.router.navigateByUrl('/signin?page=myaccount');
        }
    }

    ngOnInit(): void {
    }

}
