import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { Lightbox } from 'ngx-lightbox';
import { ToastrService } from 'ngx-toastr';
import { AuthService } from 'src/app/service/auth.service';
import { CommonService } from 'src/app/service/common.service';
import { GlobalService } from 'src/app/service/global.service';
import { SettingService } from 'src/app/service/setting.service';
import { environment } from 'src/environments/environment';
import Swal from 'sweetalert2';
declare const $: any;

@Component({
    selector: 'app-wishlist',
    templateUrl: './wishlist.component.html',
    styleUrls: ['./wishlist.component.scss']
})

export class WishListComponent implements OnInit {

    // action: boolean = true;
    assetsURL = environment.assetsURL;
    isSpinner: boolean = false;
    isReadMore: boolean = true;
    wishlist: boolean = true;
    isDataLoading: boolean = false;
    productDetails: any = {};
    wishListData: any = [];
    wishListItem: any = [];
    wishlistName: any;
    paramsData: any;
    userDetails: any;
    actionPermissions: any = {};
    wishlistObj: any = {
        id: '',
        name: '',
        user_id: ''
    };

    constructor(public gs: GlobalService,
        private toastr: ToastrService,
        private router: Router,
        private _lightbox: Lightbox,
        public auth_service: AuthService,
        public location: Location,
        public _route: ActivatedRoute,
        public commonService: CommonService,
        public setService: SettingService,
    ) {
        this.gs.updatePageName('Wishlist');
    }

    ngOnInit(): void {
        this.gs.loadScript();
        this._route.queryParamMap.subscribe((params) => {
            this.paramsData = params.get('bcid');

            if (this.paramsData != null) {
                this.getWishList(this.paramsData);
                this.getIndividualByID(this.paramsData);
            } else {
                this.getWishList(this.gs.loginData['_id']);
            }
        });

        if (this.gs.loginData && this.gs.loginData['_id']) {
            this.setService.getPermission({ id: this.gs.loginData['_id'] }).subscribe((resp: any) => {
                this.actionPermissions = resp['data'];
            }, (error: any) => {
                console.log(error);
            });
        }

    }

 // Get Id by individual
    getIndividualByID(id: any) {
        this.auth_service.getIndividualByID({ id }).subscribe((resp: any) => {
            if (resp['status']) {
                this.userDetails = resp.data;
            }
        }, (error: any) => {
            // this.isDataLoading = false;
        });
    }

    // Get WishList 
    getWishList(id: any) {
        this.isDataLoading = true;

        this.gs.getMyWishlist(id).subscribe((resp: any) => {
            this.wishListItem = resp.data;
            this.isDataLoading = false;
        }, (error: any) => {
            this.isDataLoading = false;
        });
    }

    // Add To Cart WishList
    addToCartWishlist(row: any) {
        this.isSpinner = true;

        this.gs.addToCartWishlist({
            user_collection_seller_id: row.user_collection_seller_id,
            quantity: row.quantity,
            product_id: row._id,
            individual: row.individual,
            ship_from: row.ship_from,
            create_user: this.paramsData ? this.paramsData : this.gs.loginData['_id']
        }).subscribe((resp: any) => {
            this.isSpinner = false;

            if (resp.status) {
                for (let i in this.wishListData) {
                    if (this.wishListData[i]._id == row._id) {
                        this.wishListData.splice(i, 1);
                    }
                }
                this.toastr.success('Added to Cart', 'Success', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
                this.router.navigate(['/home/cart']);
            }
        }, (error: any) => {
            console.log(error);
        });
    }

    createWisshlist(row: any) {
        if (row && row._id) {
            this.wishlistObj.name = row.collection_name
            this.wishlistObj.id = row._id
            this.wishlistObj.user_id = row.user_id
        } else {
            this.wishlistObj.name = ''
            this.wishlistObj.id = ''
            this.wishlistObj.user_id = ''
        }
        $('#create-wishlist').modal('show');
    }

    addWishlist(form: any) {
        if (form.valid) {
            if (this.wishlistObj && this.wishlistObj.id) {
                this.isSpinner = true;
                this.gs.editWishlist(this.wishlistObj).subscribe((resp: any) => {
                    this.isSpinner = false;
                    if (resp.status) {
                        this.toastr.success('Wishlist updated successFully', 'Success', {
                            timeOut: 5000,
                            positionClass: 'toast-top-right',
                            progressBar: true,
                            progressAnimation: 'increasing'
                        });
                        this.getWishList(this.gs.loginData['_id']);
                        $('#create-wishlist').modal('hide');
                    } else {
                        this.toastr.error(resp['message'], 'Oops!', {
                            timeOut: 5000,
                            positionClass: 'toast-top-right',
                            progressBar: true,
                            progressAnimation: 'increasing'
                        });
                    }
                }, (error: any) => {
                    this.isSpinner = false;
                    console.log(error);
                });
            } else {
                this.isSpinner = true;
                this.gs.createWishlist({
                    name: this.wishlistObj.name,
                    create_user: this.paramsData ? this.paramsData : this.gs.loginData['_id']
                }).subscribe((resp: any) => {
                    this.isSpinner = false;
                    if (resp.status) {
                        resp.data.count = 0
                        this.wishListItem.push(resp.data);

                        this.toastr.success('Wishlist created successfully', 'Success', {
                            timeOut: 1000,
                            positionClass: 'toast-top-right',
                            progressBar: true,
                            progressAnimation: 'increasing'
                        });

                        $('#create-wishlist').modal('hide');
                    } else {
                        this.toastr.error(resp.message, 'warning', {
                            timeOut: 1000,
                            positionClass: 'toast-top-right',
                            progressBar: true,
                            progressAnimation: 'increasing'
                        });
                    }
                }, error => {
                    this.isSpinner = false;
                });
            }
        }
    }

    // Browse Wishlist
    browse(key: any, row: any) {
        if (!key) {
            this.isDataLoading = true;
            this.wishlistName = row.collection_name;
            this.gs.browseWishlist(row._id).subscribe((resp: any) => {
                if (resp.status) {
                    this.wishListData = resp.data;
                    for(let i in this.wishListData){
                        if(!(this.wishListData[i] && this.wishListData[i].main_image)){
                            this.wishListData[i].main_image = "0";
                        }
                    }
                } else {
                    this.wishListData = [];
                }
                this.isDataLoading = false;
            }, (error: any) => {
                this.wishListData = [];
                this.isDataLoading = false;
            });
        }

        this.wishlist = key;
    }

    // Delete Wishlist
    deleteWishlist(id: any) {
        Swal.fire({
            title: 'Are you sure?',
            text: 'You will not be able to recover this wishlist!',
            icon: 'warning',
            showCancelButton: true,
            showLoaderOnConfirm: true,
            confirmButtonText: 'Yes, delete it!',
            cancelButtonText: 'No, keep it'
        }).then((result) => {
            if (result.isConfirmed) {
                this.gs.deleteWishListItem(id).subscribe((resp: any) => {
                    if (resp['message'] == "success!") {
                        Swal.fire(
                            'Deleted!',
                            'Deleted successfully.',
                            'success'
                        )

                        for (let i in this.wishListItem) {
                            if (this.wishListItem[i]._id == id) {
                                this.wishListItem.splice(i, 1);
                            }
                        }
                    } else {
                        this.toastr.error(resp['message'], 'Oops!', {
                            timeOut: 5000,
                            positionClass: 'toast-top-right',
                            progressBar: true,
                            progressAnimation: 'increasing'
                        });
                    }
                });

            } else if (result.dismiss === Swal.DismissReason.cancel) {
                // Swal.fire(
                // 	'Cancelled',
                // 	'Your imaginary file is safe :)',
                // 	'error'
                // )
            }
        });
    }

    // Delete Wishlist Item
    deleteWishlistItem(item: any) {
        Swal.fire({
            title: 'Are you sure?',
            text: 'You will not be able to recover this wishlist Item!',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Yes, delete it!',
            cancelButtonText: 'No, keep it'
        }).then((result) => {
            if (result.isConfirmed) {
                this.gs.deleteWishProduct({
                    product_id: item._id,
                    user_collection_seller_id: item.user_collection_seller_id
                }).subscribe((resp: any) => {
                    if (resp.status) {
                        Swal.fire(
                            'Deleted!',
                            'Deleted successfully.',
                            'success'
                        )
                        for (let i in this.wishListData) {
                            if (this.wishListData[i]._id == item._id) {
                                this.wishListData.splice(i, 1);
                            }
                        }
                    } else {
                        this.toastr.error(resp['message'], 'Oops!', {
                            timeOut: 5000,
                            positionClass: 'toast-top-right',
                            progressBar: true,
                            progressAnimation: 'increasing'
                        });
                    }
                });

            } else if (result.dismiss === Swal.DismissReason.cancel) {
                // Swal.fire(
                // 	'Cancelled',
                // 	'Your imaginary file is safe :)',
                // 	'error'
                // )
            }
        });
    }

    updateQuantity(item: any) {
        if (!item.quantity || item.quantity == 0) {
            item.quantity = item.price.min_quantity || 1;
        } else {
            if (item && item.quantity < item.price.min_quantity) {
                item.quantity = item.price.min_quantity || 1;
                this.toastr.warning('Please enter minimum ' + item.price.min_quantity + ' quantity', 'Warning', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        }
    }

    showText() {
        this.isReadMore = !this.isReadMore
    }

    navigateProduct(Row: any) {
        this.router.navigate(['/home/single-product', Row._id, Row._id]);
        $('#view-modal-container').modal('hide');
    }

    //View product Detail Modal
    viewProductDetail(row: any) {
        this.productDetails = row;
        $('#view-modal-container').modal('show');
    }

    // Open lightbox
    openLightbox(row: any, key: any, index: number): void {
        let finalData = this.auth_service.lightboxImage(row, key)
        this._lightbox.open(finalData, index, { wrapAround: true, showImageNumberLabel: true });
    }

    // close lightbox programmatically
    close(): void {
        this._lightbox.close();
    }

    // cart Quantity
    cartQuantity(row: any, quantity: number) {
        if (quantity == 1) {
            row.quantity++
        } else {
            if (row && row.quantity > row.price.min_quantity) {
                row.quantity--
            } else {
                this.toastr.warning('Minimum ' + row.price.min_quantity + ' quantity required', 'Warning', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        }
        // for (let i in this.cartData) {
        //     this.cartData[i].total_price = (this.cartData[i].quantity * this.cartData[i].price.selling_price || 0)
        // }
        // this.grandTotal = this.cartData.map((t: any) => Number(t.total_price)).reduce((acc: any, value: any) => acc + value, 0);

    }
}
