import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { GlobalService } from 'src/app/service/global.service';
import { SettingService } from 'src/app/service/setting.service';
import { UploadfileService } from 'src/app/service/uploadfile.service';
import { environment } from 'src/environments/environment';
declare const $: any;

@Component({
    selector: 'app-my-profile',
    templateUrl: './my-profile.component.html',
    styleUrls: ['./my-profile.component.scss']
})
export class MyProfileComponent implements OnInit {
    
    action: boolean = true;
    assetsURL = environment.assetsURL;
    isSpinner: boolean = false;
    actionPermissions: any = {};
    formObj: any = {
        firstname:'',
        lastname:'',
        aboutme: '',
        profile: '',
    }
    
    imageView: any;
    loginData: any;
    isLoading: boolean = false;

    constructor(public gs: GlobalService,
        private uploadSrv: UploadfileService,
        public toastr: ToastrService,
        public setService: SettingService,) { 
        }

    ngOnInit(): void {
        this.loginData = this.gs.loginData;
        if (this.loginData) {
            this.formObj.aboutme = this.loginData.aboutme || '';
            this.formObj.profile = this.loginData.profile || '';
            this.formObj.firstname = this.loginData.firstname || '';
            this.formObj.lastname = this.loginData.lastname || '';
        }
        this.gs.updatePageName('Profile');

        if(this.gs.loginData && this.gs.loginData['_id']){
            this.setService.getPermission({ id: this.gs.loginData['_id'] }).subscribe((resp: any) => {
                // console.log("resp >>", resp);
                this.actionPermissions = resp['data'];
            }, (error: any) => {
                console.log(error);
            });
        }
    }

    editProfile(key: boolean){
        this.action = key;
    }

    updateProfile(form: any) {
        if (form.valid) {
            let data = {
                id: this.loginData._id,
                firstname: this.formObj.firstname,
                lastname: this.formObj.lastname,
                aboutme: this.formObj.aboutme,
                profile: this.formObj.profile,
                type: 'profile'
            }
            this.isSpinner = true;
            this.gs.updateProfile(data).subscribe((resp: any) => {
                this.isSpinner = false;
                if (resp.status) {
                    let data = JSON.parse(localStorage.getItem('buyerData') || '');
                    data.aboutme = this.formObj.aboutme;
                    data.profile = this.formObj.profile;
                    data.firstname = this.formObj.firstname;
                    data.lastname = this.formObj.lastname;

                    this.gs.loginData = data;
                    this.action = true;
                    localStorage.setItem('buyerData', JSON.stringify(data));
                    
                    this.toastr.success('Profile updated successfully', 'Success', {
                        timeOut: 3000,
                        positionClass: 'toast-top-right',
                        progressBar: true,
                        progressAnimation: 'increasing'
                    });
                } else {
                    this.toastr.error(resp['message'], 'Oops!', {
                        timeOut: 3000,
                        positionClass: 'toast-top-right',
                        progressBar: true,
                        progressAnimation: 'increasing'
                    });
                }
            }, (error: any) => {
                console.log(error);
                this.isSpinner = false;
            });
        } else {
            console.log('form invalid');
        }
    }

    // Image View Modal Popup
    imageViewModal(value: any) {
        $('#imageViewModal').modal('show');
        this.imageView = value;
    }

    // Image View Modal Close 
    moddalClose() {
        $('#imageViewModal').modal('hide');
    }

    // Upload profile picture
    profile_photo_upload(event: Event) {
        const target = event.target as HTMLInputElement;
        const file: File = (target.files as FileList)[0];
        const FileSize = file.size / Math.pow(1024, 2) // in MB

        if (FileSize >= 2) {
            try {
                (<HTMLInputElement>document.getElementById('profile-photo')).value = '';
            } catch (ee) { }
            this.toastr.error('File size is too Large, Maximum 2 mb Allowed', '', {
                timeOut: 3000,
                positionClass: 'toast-top-right',
                progressBar: true,
                progressAnimation: 'increasing'
            });
        } else {          
         this. isLoading =true ;  
            this.uploadSrv.saveimage(file).subscribe((data: any) => {
                console.log('data',data);
                this.formObj.profile = data.file;
                this. isLoading =false ;  
            }, error => {
                console.log('error');
            });
        }
    }

}
